.travel {
   position: relative;
   margin-top: 100px;
   padding-bottom: 180px;
   &__btn-wrap {
      margin-top: 70px;
      text-align: center; }

   &__header {
      position: relative;
      z-index: 3;
      margin-top: 120px;
      text-align: right; }

   &__arrows {
      margin-top: 20px;
      text-align: right; }

   &__slider {
      &-body {
         position: relative;
         left: -250px;
         margin-top: -210px; }
      &-item {
         background-repeat: no-repeat;
         background-size: cover;
         width: 100%;
         padding-top: 55%;
         box-shadow: 0 0 40px 30px #171919 inset, 0 0 100px 100px #171919 inset;
         opacity: .6; }

      &-header {
         margin-top: -50px; } }

   &__loader {
      width: 100%;
      height: 100px; }

   &__info {
      position: relative;
      display: flex;
      justify-content: space-around;
      margin-top: 120px;
      height: 200px;

      &-item {
         position: relative;
         width: 25%; }

      &-aside {
         left: 0;
         top: -22px;
         transform: rotate(-90deg) translate(-100%, 100%);
         opacity: .5; }

      &-destination {
         margin-left: 36px;
         margin-top: 0;
         display: block; }

      &-message {
         position: absolute;
         left: 50%;
         top: 50%;
         transform: translate(-50%, -50%);
         font-size: 24px;
         font-weight: 500;
         letter-spacing: 5px;
         text-align: center;
         opacity: .7; }

      &-details {
         box-sizing: border-box;
         width: 100%;
         padding-left: 33px;
         padding-right: 15px;
         // text-align: center
         font-weight: 800;
         font-size: 40px; }

      &-time {
         display: block; } }

   &__img {
      &-plane {
         position: absolute;
         right: -50px;
         transform: translateY(-300px); }
      &-coctail {
         position: absolute;
         bottom: 0;
         right: 0;
         transform: translateX(35%); } } }

