.surf {
   position: relative;
   &__location {
      text-align: right; }
   &__current {
      display: block; }

   &__content {
      transform: translateY(-30px); }

   &__map {
      position: relative;
      box-sizing: border-box;
      width: 100%;
      padding: 0 80px;
      &::before {
         @include pseudo;
         content: attr(data-coords);
         right: 0;
         bottom: 50%;
         transform-origin: right;
         transform: rotate(-90deg) translate(50%, -10px);
         font-size: 18px;
         text-align: right;
         letter-spacing: 8px;
         opacity: 0.5; } }

   &__world-map {
      width: 100%; }

   &__icon {
      &-location {
         position: absolute;
         display: none;
         width: 16px;
         height: 13px;
         opacity: .4;
         fill: white;
         &_big {
            position: absolute;
            width: 295px;
            height: 350px;
            right: 0;
            top: 220px;
            transform: translateX(50%);
            opacity: .03; } }
      &-point {
         position: absolute;
         width: 24px;
         height: 33px;
         fill: $accent;
         transform: translate(-50%, -50%);
         cursor: pointer; }

      &-shadow {
         position: absolute;
         width: 20px;
         height: 20px !important;
         border-radius: 50%;
         box-shadow: 0px 0px 30px 5px rgba(74, 246, 205, 0.837777);
         background-color: $accent;
         transform: translate(-10px, -5px); } }

   &__dots {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      height: 100%;
      .slick-list {
         overflow: visible; }
      .slick-slide div {
         height: 1px; } }

   &__popup {
      position: absolute;
      animation: popup .5s; }

   &-popup {
      box-sizing: border-box;
      width: 283px;
      padding: 30px;
      background: rgba(#0F1010, 0.7);
      border-radius: 6px;

      &__header {
         display: flex;
         align-items: center;
         justify-content: space-between;
         margin-bottom: 15px; }


      &__title {
         display: block;
         position: relative;
         font-weight: 800;
         font-size: 18px;
         &::before {
            @include pseudo;
            left: 0;
            bottom: -7px;
            width: 80px;
            height: 1px;
            background-color: $accent; } }

      &__subtitle {
         text-transform: none; }

      &__icon {
         &-arrow {
            width: 16px;
            height: 13px; } }

      &__weather {
         display: flex;
         justify-content: space-between;
         margin-top: 30px;
         padding: 0 8px;

         &-item {
            display: flex;
            flex-direction: column;
            align-items: center; }

         &-icon {
            height: 24px;
            width: 100%; }

         &-value {
            display: block;
            margin-top: 8px;
            font-weight: 800; }

         &-description {
            display: block;
            margin-top: 8px;
            font-size: 10px;
            font-weight: 300; } } }

   &__slide {
      width: 110%;
      &:hover {
         .surf__slide {
            &-inner::before {
               background-color: rgba(#1C2121, .3); }
            &-title {
               transform: translateY(-10px); }
            &-link {
               opacity: 1; } } }

      &-inner {
         position: relative;
         display: flex;
         flex-direction: column;
         justify-content: space-between;
         box-sizing: border-box;
         padding: 72px 40px 0px;
         height: 750px;
         width: 100%;
         min-width: 390px;
         background-repeat: no-repeat;
         background-size: cover;
         &::before {
            @include pseudo;
            @include overlay;
            transition: .3s;
            background-color: rgba(#1C2121, .75); } }
      &-title {
         position: relative;
         margin: 0;
         width: 20%;
         font-weight: 800;
         font-size: 40px;
         line-height: 36px;
         transition: .3s;
         transform: translateY(0); }
      &-aside {
         left: 40px;
         bottom: 80px; }

      &-link {
         position: relative;
         align-self: center;
         opacity: 0;
         transition: opacity .3s; } }

   &__slider {
      margin-top: 30px;
      &-arrows {
         text-align: right; }
      .slick-list {
         padding-bottom: 80px; }
      .slick-current {
         .surf__slide {
            &-inner::before {
               background-color: rgba(#1C2121, .3); }
            &-link {
               opacity: 1; } } }
      .slick-slide {
         transform: translateY(0);
         &:nth-child(4n+1) {
            transform: translateY(32px); }
         &:nth-child(4n+3) {
            transform: translateY(90px); } } } }


@keyframes popup {
   from {
      opacity: 0; }
   to {
      opacity: 1; } }



