html, body {
   height: 100%; }

body {
   position: relative;
   min-width: 320px;
   background-color: #171919;
   color: #fff;
   font-family: 'fira sans';
   font-size: 16px;
   text-transform: uppercase;
   overflow-x: hidden; }

.wrapper {
   width: calc(100% - 160px);
   max-width: 1600px;
   margin: 0 auto; }

img {
   max-width: 100%;
   -webkit-user-select: none;
   -moz-user-select: -moz-none {
    /*IE10*/
    -ms-user-select: none;
    user-select: none;

    /*Android and not desktop browsers*/
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0); } }
a {
   text-decoration: none;
   color: inherit; }

.icon {
   width: 1rem;
   height: 1rem; }

.section {
   padding-top: 157px;
   &-title {
      display: inline-block;
      position: relative;
      margin-top: 0;
      margin-left: 165px;
      font-size: 80px;
      font-weight: 800;
      &::after {
         @include pseudo;
         content: attr(data-title);
         font-size: 200px;
         font-weight: 800;
         opacity: .02;
         left: -295px;
         top: 0px; }
      &::before {
         @include pseudo;
         width: 80px;
         height: 1px;
         background-color: $accent;
         right: 0; } } }

.title {
   margin-top: 15px;
   font-size: 40px;
   font-weight: 800;
   span {
      font-weight: 300;
      opacity: .5; } }

.suptitle-right,
.suptitle-left {
   display: inline-block;
   position: relative;
   margin-right: -7.3px;
   font-size: 22px;
   opacity: .5;
   letter-spacing: 7.3px;
   &::before {
      @include pseudo;
      width: 80px;
      height: 1px;
      top: 50%;
      background-color: rgba(#fff, 0.5); } }
.suptitle {
   &-right {
      &::before {
         left: -105px; } }
   &-left {
      &::before {
         right: -105px; } } }

.aside {
   position: absolute;
   transform-origin: left bottom;
   transform: rotate(-90deg) translateY(100%);
   font-size: 18px;
   letter-spacing: 8px;
   white-space: nowrap; }

.slick-slide {
   outline: none; }

.btn {
   display: inline-block;
   position: relative;
   font-weight: 800;
   font-size: 40px;
   line-height: 36px;
   &:hover {
      .btn-arrow {
         animation: arrow 1s linear infinite; } }
   &-marked {
      position: relative;
      display: inline-block;
      margin-left: 18px;
      padding: 27px 57px 27px 17px;
      background-color: $accent;
      &::before {
         @include pseudo;
         width: 20px;
         height: 1px;
         left: 0;
         top: 50%;
         background-color: #fff;
         transform: translateX(-50%); } }
   &-arrow {
      position: absolute;
      right: 16px;
      top: 50%;
      width: 24px;
      height: 20px;
      transform: translate(0, -50%); } }

.arrow {
   width: 24px;
   height: 20px;
   cursor: pointer;
   &-left {
      margin-right: 20px;
      transform: scaleX(-1); }
   &-dark {
      transition: opacity .3s;
      opacity: .25;
      &:hover {
         opacity: 1; } } }

.visibility-hidden {
   visibility: hidden; }

@keyframes arrow {
   from {
      transform: translate(-5px, -50%); }
   to {
      transform: translate(10px, -50%); } }

/*======================== MEDIA QUERIES ===============================*/


@media (max-width: 1200px) {
   .section {
      padding: 40px 0;
      &-title {
         margin-bottom: 30px;
         font-size: 28px; } } }

@media (max-width: 992px) {
   .section {
      padding: 30px 0;
      &-title {
         margin-bottom: 25px;
         font-size: 26px; } } }

@media (max-width: 768px) {
   .section {
      padding: 20px 0;
      &-title {
         margin-bottom: 20px;
         font-size: 24px; } } }
